.nav {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  background: #fff;
  width: 100%;
  height: 75px;
  position: fixed;
  top: 0;
  z-index: 30;
  box-shadow: 2px 0 3px rgba(0, 0, 0, 0.4);

  .logo {
    margin: 0 0 0 30px;
    padding: 0;
    color: #000;
  }

  .logo-link {
    letter-spacing: 2px;
    text-decoration: none;
    color: #000;
    font-size: 1.8rem;
    text-transform: uppercase;
    font-weight: 800;
    font-family: 'Heebo', sans-serif;
  }

  .ul-nav {
    margin-right: 30px;

    li {
      display: inline-block;
      margin: 0 10px;
      &.user {
        color: #000;
      }
      i {
        cursor: pointer;
        font-size: 18px;
        color: #000;
      }
    }
    .shop-btn {
      position: relative;

      .shop-items {
        position: absolute;
        font-size: 10px;
        font-weight: bold;
        top: -8px;
        right: -12px;
        background: red;
        color: #fff;
        padding: 1px 5px;
        border-radius: 50%;
      }
    }
  }
}
